@import url('https://fonts.googleapis.com/css2?family=Dongle:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dongle:wght@300&display=swap');
@import './features/Quotes/quotes';
@import './features/AudioPlayer/audio';


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#root {
  width: 100vw;
  height: 100vh;
  background-image: url("../public/images/bg.jpg");
}

body {
  overflow: hidden;
}

button {outline: none; border: none; background-color: transparent; cursor: pointer}

li { list-style-type: none }

header {
  display: flex;
  justify-content: space-between;
  min-height: 15%;
}

.buttons-panel {
  display: flex;
  filter: invert(100%);
  button {

    background-size: cover;
    background-color: transparent;
  }

  .other {
    background-image: url("../public/images/other.png");
    width: 30px;
    height: 30px;
    margin-top: 2px;
  }
  .current {
    background-image: url("../public/images/pause.png");
    width: 35px;
    height: 35px;
  }
  .next {
    transform: rotate(180deg);
  }
}

main {
  //position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 60%;
  width: 100%;
  margin: 0 auto;
}

.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  font-size: 55px;
  color: white;

  .current-date {
    display: flex;
    flex-direction: column;
  }


  .welcome-phrase {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 30px;
    width: 100vw;
    opacity: 0;
    transition: opacity 2s ease-in-out;
    span {
      width: 50%;
      text-align: right;
    }
    &.show {
      opacity: 1;
    }
  }

  .name-field {
    margin-left: 10px;
    border: none;
    outline: none;
    color: white;
    height: 66px;
    width: 50%;
    font-size: 30px;
    background-color: transparent;
  }
}

.modals-nav {
  display: flex;
  gap: 15px;
  height: 25px;

  button {
    height: 100%;
    color: white;
    font-size: 15px;
    border-bottom: 2px solid transparent;
    transition: border-color 0.3s ease-in-out;
    &:hover, &:active {
      border-color: rosybrown;
    }
  }
}

footer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 35%;

  ul {
    display: flex;
    justify-content: center;
    padding: 0 0 20px 20px;
    flex-direction: column;
    width: 100%;
    height: 100%;
    font-family: 'Dongle', sans-serif;
  }
}

.timer {
  font-size: 75px;
  height: 60px;
  color: white;
}

.date {
  font-size: 35px;
  color: white;
}



.modal {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: white;
  transition: height 0.5s ease-in-out;
  overflow: hidden;

  &.active {
    bottom: 0;
    height: 60vh;
    padding-bottom: 50px;
  }

  .close-modal {
    display: grid;
    place-items: center;
    font-size: 30px;
    margin: 0 auto;
    width: 60px;
    height: 40px;
    z-index: 10000;
    background-color: rgba(255, 255, 255, 0.87);
    border-radius: 20% 20% 0 0;
    position: relative;
  }
}

.modal-content {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.87);
}

.apply-city {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.get-weather {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 350px;


  button {
    border: 2px solid black;
    padding: 10px;
    box-shadow: 2px 2px;
    &:active {
      transform: translateY(4px);
      box-shadow: none;
    }
  }
}

#city {
  font-size: 20px;
  margin-bottom: 10px;
  font-family: 'Dongle', sans-serif;
  outline: none;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-bottom: 1px solid black;
}

.error-container {
  //width: 70%;
  position: absolute;
  top: -100px;
  left: 0;
  padding: 5px;
  border: 1px solid darkred;
  background-color: red;
  color: white;
  font-weight: 600;
  font-size: 15px;
}

.weather-container {
  position: relative;
  width: 100%;
  height: 100%;

  input {
    position: absolute;
    left: 15px;
    top: 15px;
  }
}

.weather-grid {
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
}

@media only screen and (max-width: 768px) {
  .welcome-phrase {
    span { font-size: 40px }
    .name-field { font-size: 40px }
  }
  .timer {
    font-size: 40px;
  }
  .date {
    font-size: 30px;
  }
  .modals-nav button {
    font-size: 17px;
  }
  .timer {
    height: 40px;
  }
}

@media only screen and (max-width: 576px) {
  .welcome-phrase {
    span { font-size: 25px }
    .name-field { font-size: 25px }
  }
  .container {
    grid-template-columns: 1fr;
  }
  .modals-nav button {
    font-size: 15px;
  }
  .timer {
    height: 32px;
  }
}

@media only screen and (max-width: 480px) {
  .welcome-phrase {
    span { font-size: 30px }
    .name-field { font-size: 30px }
  }

  .modals-nav button {
    font-size: 13px;
  }
  .timer {
    height: 30px;
  }
}


@media only screen and (max-width: 320px) {
  .welcome-phrase {
    span { font-size: 17px }
    .name-field { font-size: 17px }
  }

  .banner {
    height: 150px;
  }
  .modals-nav button {
    font-size: 9px;
  }
  .timer {
    height: 30px;
  }
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 10px solid #f3f3f3; /* Light grey */
  border-top: 10px solid #383636; /* Blue */
  border-radius: 50%;
  animation: spinner 1.5s linear infinite;
}

.spinner-container {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 350px;
}